import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  FStatsCashboxFragment,
  useQStatsCashboxes,
} from '../../../graph/generated'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { i18n } from '../../../i18n'
import { StatisticsBaseMultiFilter } from './StatisticsBaseMultiFilter'

type TCashbox = FStatsCashboxFragment

function formatCashboxName({ name, uuid }: TCashbox) {
  return name || uuid
}

export const StatisticsCashboxFilter = observer<NoChildren>(() => {
  const { statistics } = useRoot()
  const { data, loading } = useQStatsCashboxes({
    branches: statistics.branchesFilter,
  })
  const getOptions = useLoadingCache<RoA<TCashbox>>()
  return (
    <StatisticsBaseMultiFilter<TCashbox>
      placeholder={i18n.t`Filter by cashbox`}
      options={getOptions(loading, () => data.cashBoxes)}
      selected={statistics.cashboxes}
      formatOptionLabel={formatCashboxName}
      onChange={statistics.setCashboxFilter}
    />
  )
})
