import { idProp } from '@speedlo/tools'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { SelectControl } from '../../../atoms/SelectControl'

type TProps<TOption> = NoChildren & {
  placeholder: string
  options: RoA<TOption> | null
  selected: RoA<ID>
  onChange(selected: RoA<ID>): void
  formatOptionLabel(option: TOption): string
}

const NO_OPTIONS = []

export const StatisticsBaseMultiFilter = <TOption extends { id: ID }>({
  selected,
  options,
  placeholder,
  onChange,
  formatOptionLabel,
}: TProps<TOption>) => {
  return useObserver(() => (
    <SelectControl<TOption>
      isMulti
      isClearable
      isSearchable
      placeholder={placeholder}
      isLoading={options === null}
      options={options || NO_OPTIONS}
      value={
        options === null
          ? NO_OPTIONS
          : options.filter(option => selected.includes(option.id))
      }
      formatOptionLabel={formatOptionLabel}
      filterOption={(option, rawInput) =>
        rawInput.length === 0 ||
        formatOptionLabel(option.data)
          .toLocaleLowerCase()
          .includes(rawInput.toLocaleLowerCase())
      }
      getOptionValue={idProp}
      onChange={value => {
        if (!value) {
          onChange([])
        } else {
          onChange((value as TOption[]).map(idProp))
        }
      }}
    />
  ))
}
