import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { FDriverFragment, useQAllDrivers } from '../../../graph/generated'
import { formatFullName } from '../../../helpers/formats'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { i18n } from '../../../i18n'
import { StatisticsBaseMultiFilter } from './StatisticsBaseMultiFilter'

type TDriver = FDriverFragment

export const StatisticsDriversFilter = observer<NoChildren>(() => {
  const { statistics } = useRoot()
  const { data, loading } = useQAllDrivers({
    branches: statistics.branchesFilter,
  })
  // ? Why this?
  // React.useEffect(() => {
  //   if (!loading) {
  //     statistics.setDriversFilter(
  //       RM.intersection(statistics.drivers, data.drivers.map(idProp)),
  //     )
  //   }
  // }, [data, loading, statistics])
  const getOptions = useLoadingCache<RoA<TDriver>>()
  return (
    <StatisticsBaseMultiFilter<TDriver>
      placeholder={i18n.t`Filter by drivers`}
      formatOptionLabel={formatFullName}
      options={getOptions(loading, () => data.drivers)}
      selected={statistics.drivers}
      onChange={statistics.setDriversFilter}
    />
  )
})
