import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  FStatsCashierFragment,
  useQStatsCashiers,
} from '../../../graph/generated'
import { formatFullName } from '../../../helpers/formats'
import { useLoadingCache } from '../../../hooks/useLoadingCache'
import { i18n } from '../../../i18n'
import { StatisticsBaseMultiFilter } from './StatisticsBaseMultiFilter'

type TCashier = FStatsCashierFragment

export const StatisticsCashiersFilter = observer<NoChildren>(() => {
  const { statistics } = useRoot()
  const { data, loading } = useQStatsCashiers({
    branches: statistics.branchesFilter,
  })
  const getOptions = useLoadingCache<RoA<TCashier>>()
  return (
    <StatisticsBaseMultiFilter<TCashier>
      placeholder={i18n.t`Filter by cashier`}
      options={getOptions(loading, () => data.cashiers)}
      selected={statistics.cashiers}
      formatOptionLabel={formatFullName}
      onChange={statistics.setCashierFilter}
    />
  )
})
