import React from 'react'

import { formatPrice } from '../helpers/formats'

interface IPriceProps {
  children?: number
  price?: number
  currencyCode?: string
  className?: string
}

export const Price: React.FC<IPriceProps> = ({
  children,
  price,
  currencyCode,
  className,
}) => {
  const formattedPrice = formatPrice(children || price)
  return (
    <span className={className}>
      {currencyCode ? `${formattedPrice} ${currencyCode}` : formattedPrice}
    </span>
  )
}
