import React from 'react'

import { useStatsFilter } from '../hooks/useStatsFilter'

type TProps = Required<SomeChildren> & {
  autoFilter?: string
}

export const OrdersStatisticsLoader: React.FC<TProps> = ({
  children,
  autoFilter,
}) => {
  const { executeFilter, renderForm } = useStatsFilter('driver')

  React.useEffect(() => {
    if (autoFilter) {
      executeFilter()
    }
  }, [autoFilter, executeFilter])

  return renderForm(children)
}
