import { i18n } from '@lingui/core'
import { DateTimePicker, DateTimePickerProps } from '@material-ui/pickers'
import React from 'react'

import { DateTimeFormatProvider } from './DateTimeFormatProvider'

export const InputDateTime: React.FC<
  DateTimePickerProps & NoChildren
> = props => {
  return (
    <DateTimeFormatProvider>
      <DateTimePicker
        hideTabs
        variant="inline"
        format="lll"
        maxDateMessage={i18n.t`Date is too much in the future`}
        minDateMessage={i18n.t`Date is too much in the past`}
        {...props}
      />
    </DateTimeFormatProvider>
  )
}
